*, *:before, *:after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  -webkit-tap-highlight-color: transparent;
  user-select: none;
  outline: none;
  backface-visibility: hidden;
}

@font-face {
  font-family: 'Exo2';
  src: url(./assets//fonts/Exo2-Regular.ttf);
}
@font-face {
  font-family: 'Exo2-Medium';
  src: url(./assets//fonts/Exo2-Medium.ttf);
}
@font-face {
  font-family: 'Exo2-Bold';
  src: url(./assets/fonts/Exo2-Bold.ttf);
}

:root {
  --spacing: 20px;
}

a {
  color: inherit;
  cursor: pointer;
  text-decoration: none;
  font-family: 'Exo2-Medium', sans-serif;
  transition: .3s;
}

button {
  cursor: pointer;
  font-size: 16px;
  border-radius: 7px;
  letter-spacing: 0.03em;
  overflow: hidden;
  position: relative;
  z-index: 1;
  color: #999999;
  border: 2px solid #999999;
  background-color: #F9F9F9;
}

button, .link-button, input {
  border: none;
  font-family: 'Exo2-Medium', sans-serif;
  transition: .3s;
  text-overflow: ellipsis;
  overflow: hidden;
}

input, textarea {
  font-family: 'Exo2', sans-serif;
  padding: 15px 20px;
  font-size: 15px;
  border-radius: 7px 7px 0px 0px;
  letter-spacing: 0.02em;
  color: #999999;
  border: none;
  border-bottom: 2px inset #999999;
  background-color: #F9F9F9;
  display: block;
  width: 100%;
}

input[type="checkbox"] {
  appearance: none;
  -webkit-appearance: none;
  min-width: 34px;
  width: 34px;
  height: 22px;
  padding: 0px;
  border-radius: 10px;
  background-color: #fff !important;
  position: relative;
  cursor: pointer;
  border: none;
}
input[type="checkbox"]:after {
  content: '';
  position: absolute;
  top: 5px;
  left: 5px;
  width: 12px;
  height: 12px;
  background-color: currentColor;
  border-radius: 50%;
  transition: .2s;
  box-shadow: 0px 0px 5px rgba(0, 0, 0, .2);
}
input[type="checkbox"]:checked:after {
  transform: translateX(100%);
  transition: .2s;
}

input:hover, input:focus,
textarea:hover, textarea:focus {
  border-color: transparent !important;
}

input::placeholder,
textarea::placeholder {
  color: inherit;
  font-family: 'Exo2', sans-serif;
  opacity: .6;
}

textarea {
  height: 150px;
  transition: .3s;
  resize: none;
}

.link-icon.link-icon {
  transition: .3s;
  border-radius: 10px;
  margin: 5px;
  width: 50px;
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-bottom: 2px inset transparent !important;
}

.link-icon img {
  width: 25px;
  height: 25px;
}

.link-icon img,
.link-button img {
  pointer-events: none;
}

.link-button {
  text-align: center;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  transition: .3s;
  padding: 12px 15px;
  border-radius: 7px;
  text-decoration: none;
  border-bottom: 2px inset transparent !important;
}

.link-button.link-button img {
  width: 17px;
  height: 17px;
  margin-right: 10px;
}

.link-icon.active,
.link-button.active {
  transition: .2s;
  box-shadow: none !important;
  border-bottom-left-radius: 0px;
  border-bottom-right-radius: 0px;
  border-bottom: 2px inset currentColor !important;
}

input:hover, input:focus,
textarea:hover, textarea:focus,
.link-icon:hover, .link-icon:focus,
.link-button:hover, .link-button:focus,
button:not(.under):not(.disable):hover, button:not(.under):not(.disable):focus {
  box-shadow: inset 0 0 0 2px currentColor;
  transition: .3s;
}

button:active, a:active {
  opacity: .7;
  transition: .1s !important;
}
button img {
  pointer-events: none;
}

html {
  min-height: 100vh;
}

body {
  font-family: 'Exo2', sans-serif;
  position: relative;
  overflow: hidden;
  background-image: url('assets/img/pattern.png');
  background-repeat: repeat;
  background-position: center center;
  background-color: #5DA3C0;
}

body.freeze {
  overflow: hidden;
}

.scyle::-webkit-scrollbar {
  width: 5px;
  height: 4px;
  background-color: inherit;
}
.scyle::-webkit-scrollbar-thumb {
  background-color: #E6E6E6;
  border-radius: 9em;
}
.scyle::-webkit-scrollbar-thumb:hover {
  background-color: #DCDCDC;
}

h1, h2, h3, h4, h5, h6 {
  font-family: 'Exo2-Medium', sans-serif;
  font-weight: normal;
}

.wrapper {
  padding: 0px var(--spacing);
  max-width: 1580px;
  margin: 0 auto;
}

.under {
  border: none;
  background-color: transparent;
  text-decoration: underline;
  cursor: pointer;
}

.gray {
  border-color: #999999;
  background-color: #f7f4f4;
  color: #919191;
}

.main {
  border-color: #5DA3C0;
  background-color: #EDF9FF;
  color: #5DA3C0;
}

.success {
  border-color: #4EAE53;
  background-color: #F6FFF7;
  color: #4EAE53;
}

.danger {
  border-color: #C25A6C;
  background-color: #F9EBEE;
  color: #C25A6C;
}

.disable {
  opacity: .5;
  cursor: not-allowed;
  pointer-events: none;
}

.nb.nb.nb {
  border: none;
}

#root {
  overflow-y: scroll;
  max-height: 100vh;
}

#prompt:not(:empty) {
  position: fixed;
  top: 20px;
  width: calc(100% - 40px);
  margin: 0px 20px;
  cursor: pointer;
  display: flex;
  flex-direction: column;
  align-items: center;
  z-index: 0;
}


@media (max-width: 500px) {
  button {
    font-size: 13px;
  }
  .link-button img {
    width: 15px;
    height: 15px;
  }
  .link-button {
    font-size: 0.9em;
  }
  :root {
    --spacing: 10px;
  }
}

@keyframes deleting {
  0% {
    transform: scale3d(1, 1, 1);
    background-color: #fff;
  }
  15% {
    transform: scale3d(1.03, 0.97, 1);
  }
  30% {
    transform: scale3d(0.97, 1.07, 1);
  }
  45% {
    transform: scale3d(1.07, 0.93, 1);
    background-color: #F9EBEE;
  }
  60% {
    transform: scale3d(0.97, 1.02, 1);
  }
  75% {
    transform: scale3d(1.03, 0.97, 1);
  }
  100% {
    transform: scale3d(1, 1, 1);
    background-color: #fff;
  }
}

@keyframes on-close {
  50% {
    opacity: 0;
  }
  100% {
    transform: scale(0);
    opacity: 0;
    margin-left: 0px;
    margin-right: 0px;
    width: 0px;
    padding: 0;
  }
}

@keyframes scroll-up {
  0% {
    transform: translateX(-110%);
  }
  100% {
    transform: translateX(110%);
  }
}

@keyframes scale-down {
  0% {
    transform: scale(1.1);
  }
  100% {
    transform: scale(1);
  }
}

@keyframes fade-in {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes prompt {
  0% {
    transform: translateY(-105%);
    opacity: 0;
  }
  7% {
    transform: translateY(0%);
    opacity: 1;
  }
  93% {
    transform: translateY(-0%);
    opacity: 1;
  }
  100% {
    transform: translateY(-105%);
    opacity: 0;
  }
}

@keyframes slide-left {
  0% {
    opacity: 0;
    transform: translateX(10%);
  }
  100% {
    opacity: 1;
    transform: translateX(0%);
  }
}
